<template>
  <section class="row overflow-scroll">
    <div class="card border-dark paperDarkBg">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h3 flex-grow-1" style="font-family: BellGothicStd"
          >overview</span
        >
        <div class="h5 me-3 mt-auto d-flex align-items-center mb-0">
          <small class="text-secondary">2025/01/01</small>
          <i class="bi bi-caret-right-fill h6 mb-0 mx-2"></i>
          <small class="text-secondary">2025/01/08</small>,
          <small class="ms-3 text-secondary">weekly</small>
          <i class="bi bi-caret-down-fill h6 mb-0"></i>
        </div>
      </div>
    </div>
    <!-- 上 -->
    <template v-for="(item, index) in infoList" :key="index">
      <div class="text-uppercase border border-dark col-6 col-md-4 text-center">
        <div class="position-relative my-3">
          <h4>{{ item.title }}</h4>
          <h4>$<span class="text-secondary h3 mx-2">{{ $toCurrency(item.amount) }}</span>tw</h4>
        </div>
      </div>
    </template>
    <!-- 中 -->
    <div class="card border-dark">
      <div class="card-body">
        <C3Model />
      </div>
    </div>
    <!-- 下 -->
    <div class="card border-dark col-sm-6">
      <div class="card-body">
        <Sale />
