</template>

<script>
import C3Model from '@/components/dashboard/C3Model.vue';
import Sale from '@/components/dashboard/SaleModel.vue';
import { getToken } from '@/assets/javascript/methods';

export default {
  data() {
    return {
      infoList: [
        {
          title: 'TOTAL REVENUE',
          amount: 54540,
        },
        {
          title: 'TOTAL COST',
          amount: 12660,
        },
        {
          title: 'NET INCOME',
          amount: 41880,
        },
      ],
    };
  },
  components: {
    C3Model,
    Sale,
  },
  methods: {
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();
      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/backend');
      }
    },
  },
  created() {
    this.checkSignIn();
  },
};
</script>
